.pagewrapper{
    display: inline-block;
    box-sizing: border-box;
    border-left: 2px solid rgb(204, 203, 203);
    padding-left: 18px;
}
.imagesetting{
    display: block;
    margin: auto;
    width: 200px;
    height: 200px;
    text-align: center;
}
.imagesetting img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.musicimage{
    margin: auto;
    text-align: center;
    border-radius: 25px;
}
.musicimage img{
    height: 175px;
    width: 175px;
    border-radius: 25px;
}